<template>
  <div class="home">
    <div class="page_center">
      <div class="imgbox">
        <div class="log_web"></div>
      </div>
      <div class="innerTip">此页面仅限于内部使用</div>
      <p class="title">内部视频会议公用平台</p>
      <p class="subhead">请选择你想使用的会议模式</p>
      <ul class="list">
        <li class="first">
          <div class="img"></div>
          <div class="btnBox">
            <span class="btn1" @click="entryVideo(1, 2, 1)">发起人</span>
            <span class="btn2" @click="entryVideo(1, 1, 1)">参与人</span>
          </div>
        </li>
        <li class="second">
          <div class="img"></div>
          <div class="btnBox">
            <span class="btn1" @click="entryVideo(1, 2, 4)">发起人</span>
            <span class="btn2" @click="entryVideo(1, 1, 4)">参与人</span>
          </div>
        </li>
        <li class="third">
          <div class="img"></div>
          <div class="btnBox">
            <span class="btn1" @click="entryVideo(2, 2, '不限')">发起人</span>
            <span class="btn2" @click="entryVideo(2, 1, '不限')">参与人</span>
          </div>
        </li>
      </ul>
      <p class="tip">（每个会议室只能有一个会议主持）</p>
    </div>
    <div class="mask" v-show="isLogin || isCreated"></div>
    <!-- 创建会议室/进入会议室 -->
    <div id="loginPanel" class="login" v-show="isLogin">
      <span class="iconfont icon-close" @click="isLogin = false"></span>
      <p class="login_title">{{ dialogData.title }}</p>
      <div class="inputBox">
        <p class="input" v-show="dialogData.firstLabel">
          <label>{{ dialogData.firstLabel }}</label>
          <input v-model="email" :placeholder="dialogData.firstPlaceholder">
          <span class="inputTip" v-if="firstErrorTip">{{ firstErrorTip }}</span>
        </p>
        <p class="input" v-show="dialogData.secondLabel">
          <label>{{ dialogData.secondLabel }}</label>
          <input v-model="room_id" type="number" :placeholder="dialogData.secondPlaceholder">
          <span class="inputTip" v-if="secondErrorTip">会议室编号不能为空</span>
        </p>
        <p class="input">
          <label>参会人数：</label>
          <span>{{ member_count }}</span>
        </p>
      </div>
      <p class="errorTip" v-if="errorTip">{{ errorTip }}</p>
      <span class="btn_cancel" @click="login">{{ dialogData.btnText }}</span>
      <p class="exchange" v-show="dialogData.exchangeText" @click="exchangeDialog">{{ dialogData.exchangeText }}</p>
    </div>
    <!-- 会议室创建成功提示 -->
    <div class="login" v-if="isCreated">
      <span class="iconfont icon-close" @click="isCreated = false"></span>
      <p class="login_title">会议室生成成功<p>
      <div class="inputBox">
        <p class="input">
          <label>用户名：</label>
          <input v-model="email" placeholder="建议填写公司内常用称呼">
          <span class="inputTip" v-if="firstErrorTip">{{ firstErrorTip }}</span>
        </p>
        <p class="input">
          <label>会议室编号：</label>
          <span>{{ room_id }}</span>
        </p>
        <p class="input">
          <label>参会人数：</label>
          <span>{{ member_count }}</span>
        </p>
      </div>
      <p class="errorTip">请将您的会议室编号告知您的参与人</p>
      <span class="btn_cancel" @click="entryRoom">进入会议室</span>
      <p class="exchange" @click="exchangeDialogByCreated">切换已有会议室</p>
    </div>

  </div>
</template>

<script>
import { createRoom, entryRoom } from '../api/index.js';

const dialogData = {
  type1: { // 发起人创建会议
    title: '创建会议室',
    btnText: '生成会议室',
    exchangeText: '切换已有会议室'
  },
  type2: { // 发起人进入已有会议
    title: '已有会议室',
    firstLabel: '用户名：',
    firstPlaceholder: '建议填写公司内常用称呼',
    secondLabel: '会议室编号：',
    secondPlaceholder: '请输入会议室编号',
    btnText: '进入',
    exchangeText: '切换创建会议室'
  },
  type3: { // 参与人进入会议
    title: '进入会议室',
    firstLabel: '用户名：',
    firstPlaceholder: '建议填写公司内常用称呼',
    secondLabel: '会议室编号：',
    secondPlaceholder: '请输入会议室编号',
    btnText: '进入',
  },
};

export default {
  name: 'home',
  data(){
    return {
      isLogin: false, // 创建会议室/进入会议室 dialog
      isCreated: false, // 会议室创建成功提示 dialog
      type: '', // 1v1:1 1v4:1 直播:2
      user_type: '',// 1: 参与人，2: 发起人
      member_count: '', // 参与人数 1:1v1, 4:1v4, 0:直播
      email: '',
      room_id: '001',
      firstErrorTip: '', // 发起人不能为空 提示
      secondErrorTip: false, // 会议室编号不能为空 提示
      errorTip: false, // 创建/进入会议室的错误提示
      dialogType: 'type1', // dialog类型 
      dialogData: dialogData.type1, // dialog文本展示的数据
    }
  },

  methods: {
    entryVideo(type, user_type, member_count) {
      this.clearInput();
      this.type = type;
      this.user_type = user_type;
      this.member_count = member_count;
      this.isLogin = true;

      this.dialogType = user_type === 2 ? 'type1' : 'type3';
      this.dialogData = dialogData[this.dialogType];
    },

    exchangeDialog() {
      this.clearInput();
      this.dialogType = this.dialogType === 'type1' ? 'type2' : 'type1';
      this.dialogData = dialogData[this.dialogType];
    },

    exchangeDialogByCreated() {
      this.clearInput();
      this.isCreated = false;
      this.isLogin = true;
      this.dialogType = 'type2';
      this.dialogData = dialogData[this.dialogType];
    },

    login() {
      if(this.dialogType === 'type1') {
        const member_count = this.member_count == '不限' ? 0 : this.member_count;
        createRoom(this.type, member_count).then(res => {
          const data = res.data;
          const { room_id } = data.result;
          this.room_id = room_id;
          this.isLogin = false;
          this.isCreated = true;
        });
      } else {
        this.entryRoom();
      }
    },

    // url跳转
    entryRoom() {
      if(!this.entryRoomCheckout()) return;
      entryRoom(this.room_id, this.email, this.user_type).then(res => {
          const data = res.data;
          const url = data.result.url;
          window.location.href = url;
      });
    },

    // input校验
    entryRoomCheckout() {
      this.clearErrorTip();
      if(!this.email.trim()) {
        this.firstErrorTip = '用户名不能为空';
        return false;
      } else if(this.email.length > 30) {
        this.firstErrorTip = '长度不能超过30';
        return false;
      } else if(!this.room_id.toString().trim()) {
        this.secondErrorTip = true;
        return false;
      }
      return true;
    },

    clearInput() {
      this.email = '';
      this.room_id = '';
      this.clearErrorTip();
    },

    clearErrorTip() {
      this.firstErrorTip = '';
      this.secondErrorTip = false;
    }
  },
}
</script>

<style lang="less" scoped>
    .page_center {
      position: relative;
      width: 1024px;
      margin: 0 auto;
      .log_web {
        position: absolute;
        top: 20px;
        left: 23px;
        width: 252px;
        height: 60px;
        background-image: url(../assets/img/logo@2x.png);
        background-repeat: no-repeat;
        background-size: contain;
      }
      .innerTip {
        position: absolute;
        top: 30px;
        right: 30px;
        font-size:20px;
        font-weight:600;
        color:rgba(217,55,48,1);
        line-height:28px;
      }
      .title {
        position: absolute;
        top: 114px;
        width: 100%;
        text-align: center;
        font-size:36px;
        font-weight:600;
        color:rgba(62,113,224,1);
        line-height:50px;
      }
      .subhead {
        position: absolute;
        top: 173px;
        width: 100%;
        text-align: center;
        font-size:16px;
        font-weight:400;
        color:rgba(40,40,40,1);
        line-height:22px;
      }
      .tip {
        position: absolute;
        top: 699px;
        margin-bottom: 10px;
        width: 100%;
        text-align: center;
        font-size:20px;
        font-weight:600;
        color:rgba(217,55,48,1);
        line-height:28px;
      } 
      .list {
        position: absolute;
        left: 50%; 
        transform: translateX(-50%);
        top: 245px;
        width: 942px;
        height: 425px;
        display: flex;
        justify-content: space-between;
        li {
          flex-basis: 314px;
          position: relative;
          .img {
            width: 100%;
            height: 100%;
            background-size: 100% 100%;
          }
          .btnBox {
            position: absolute;
            top: 396px;
            left: 30px;
            width: 254px;
            height: 43px;
            display: flex;
            justify-content: space-between;
            span {
              width: 121px;
              height: 43px;
              line-height: 43px;
              border-radius:6px;
              font-size:20px;
              font-weight:600;
              color:rgba(255,255,255,1);
              cursor: pointer;
            }
          }
        }
        li.first {
          .img {
            background-image: url(../assets/img/level1bg@2x.png);
          }
          span {
            background:linear-gradient(158deg,rgba(255,193,92,1) 0%,rgba(247,107,28,1) 100%);
          }
        }
        li.second {
          .img {
            background-image: url(../assets/img/level2bg@2x.png);
          }
          span {
            background:linear-gradient(170deg,rgba(49,221,255,1) 0%,rgba(69,127,255,1) 100%)
          }
        }
        li.third {
          .img {
            background-image: url(../assets/img/level3bg@2x.png);
          }
          span {
            background:linear-gradient(159deg,rgba(255,140,150,1) 0%,rgba(243,52,80,1) 100%);
          }
        }
      }
    }

    .mask {
      position: fixed;
      width: 100%;
      height: 100%;
      top: 0;
      right: 0;
      bottom: 0;
      right: 0;
      background-color: rgba(0,0,0,0.7);
    }

    .login {
      position: fixed;
      width: 500px;
      height: 335px;
      top: 217px;
      left: 50%;
      transform: translateX(-50%);
      border-radius: 30px;
      background-color: #fff;
      .icon-close {
        position: absolute;
        right: 15px;
        top: 15px;
        font-size: 20px;
        cursor: pointer;
      }
      .login_title {
        margin-top: 24px;
        text-align: center;
        font-size: 28px;
        font-weight:600;
        color:rgba(40,40,40,1);
        line-height:45px;
      }
      .inputBox {
        width: 290px;
        // height: 100px;
        border: 1px solid #fff;
        margin: 20px auto 0;
        .input {
          margin-top: 15px;
          white-space: nowrap;
          label {
            display: inline-block;
            width: 100px;
            text-align: right;
          }
        }
        p {
          text-align: left;
        }
        span.inputTip {
          margin-left: 5px;
          font-size: 12px;
          color: red;
        }
      }
      input {
        text-indent: 0;
        outline:none;
        -webkit-appearance:none;
        box-sizing: border-box;
        width: 160px;
        height: 30px;
        border: 1px solid rgba(198,204,213,1);
        // border-radius: 32px;
        padding-left: 6px;

        font-size:12px;
        font-weight:400;
        // color:rgba(137,144,157,1);
      }
      .errorTip {
        margin-top: 15px;
        font-size: 12px;
        color: red;
      }
       span.btn_cancel {
          display: inline-block;
          margin-top: 20px;
          width: 200px;
          height: 40px;
          line-height: 40px;
          border-radius:20px;
          text-align: center;
          font-size:20px;
          color: #fff;
          background: rgba(62,113,224,1);
          cursor: pointer;
        }
        p.exchange {
          margin-top: 20px;
          color: blue;
          font-size: 12px;
          text-decoration: underline;
          cursor: pointer;
        }
     }

</style>

