import axios from 'axios';

// 创建会议室 
const createRoom = (type, member_count) => axios.post('/meeting/createRoom', { type, member_count });

// 进入会议室
const entryRoom = (room_id, user_id, user_type) => axios.post('/meeting/entryRoom', { room_id, user_id, user_type });


export { createRoom, entryRoom };